<template>
	<div id="finance-current">
		<public-header :title="title"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr">
					<div class="tab-item" :class="{active: filter.items.type == 0}" @click="onToggleType(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.type == 1}" @click="onToggleType(1)">收入</div>
					<div class="tab-item" :class="{active: filter.items.type == 2}" @click="onToggleType(2)">支出</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="current-list"
				>
					<van-cell-group>
						<template v-for="(item, index) in records.data">
							<van-cell :label="item.createtime_cn" is-link :to="util.getUrl({path: '/pages/member/current/detail', query: {id: item.id}})">
								<template slot="title">
									<span class="c-primary" v-if="item.num > 0">收入</span>
									<span class="c-danger" v-else-if="item.num < 0">支出</span>
								</template>
								<template slog="default">
									<span class="c-primary" v-if="item.num > 0">+{{item.num}}</span>
									<span class="c-danger" v-else-if="item.num < 0">{{item.num}}</span>
								</template>
							</van-cell>
						</template>
					</van-cell-group>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				title: '余额明细',
				showPreLoading:true,
				isRefresh: false,
				filter: {
					items: {
						credit: 'credit2',
						type: 0
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force) {
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'wmall/member/credit/list',
					success: (res) => {
						that.util.setWXTitle(that.title);
					}
				});
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
			onToggleType(value) {
				value = parseInt(value);
				if(this.filter.items.type == value) {
					return;
				}
				this.filter.items.type = value;
			}
		},
		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#finance-current .wrap-search{
		width: 100%;
		position: fixed;
		top: 0;
		z-index: 10;
		left: 0;
		right: 0;
	}
	#finance-current #public-header~.content .wrap-search{
		top: 46px;
	}
	#finance-current .content{
		padding-top: 46px;
	}
</style>


